.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
}

.experience__container > div{
    background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #9f7928 30%, #8A6E2F 40%, transparent 80%),
    radial-gradient(ellipse farthest-corner at left top, #FDB931 0%, #FDB931 8%, #FDB931 25%, #8A6E2F 62.5%, #5d4a1f 100%);
    padding: 2.5rem 2.5rem;
    border-radius: 2rem;
    border: 1px solid var(--color-gold);
}

.experience__container > div:hover{
    background: transparent;
    border: 1px solid var(--color-gold);
    cursor: pointer;
    color: var(--color-gold);
}

.experience__container > div:hover h3{
    text-align: center;
    color: var(--color-gold);
    margin-bottom: 2rem;
}

.experience__container > div h3{
    text-align: center;
    color: black;
    margin-bottom: 2rem;
}

.experience__container > div h5{
    text-align: center;
    color: black;
}

.experience__content{
    display: grid;
    row-gap: 2rem;
    column-gap: 2rem;
    grid-template-columns: 1fr 1fr;
}

.experience__details{
    display: flex;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 5px;
    font-size: xx-large;
}

/*Media Queries (Medium Devices)*/
@media screen and (max-width: 1440px){
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 50%;
        padding:2rem;
        margin: 0 auto;
    }

    .experience__content{
        display: grid;
        row-gap: 2rem;
        column-gap: 2rem;
        grid-template-columns: 1fr 1fr;
    }
}

/*Media Queries (Medium Devices)*/
@media screen and (max-width: 1024px){
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container > div{
        width: 80%;
        padding:2rem;
        margin: 0 auto;
    }

    .experience__content{
        display: grid;
        row-gap: 2rem;
        column-gap: 2rem;
        grid-template-columns: 1fr 1fr;
    }
}

/*Media Queries (Small Devices)*/
@media screen and (max-width: 600px){
   .experience__container{
    gap: 1rem;
   }

   .experience__container > div{
        width: 100%;
        padding: 2rem 1rem;
   }
}

